import React from "react";

export default function PloshCell( props ){
  const value = props['props']

  return (
    <div className="flex w-full flex-row items-center justify-start gap-1 text-gray-400">
      <div className="flex flex-1 flex-col items-start justify-start truncate">
        <div className="line-clamp-2">{`${value['full_living_area']} , ${value['total_living_area']} , ${value['living_area']} `}</div>
        <div className="text-xs">
          <span className='font-bold'>{value['room_count']}</span> . {`${value['type_of_settlement'] ? value['type_of_settlement'] : '-'} ${value['floor'] ? value['floor'] : "-"}`}
        </div>
      </div>
    </div>
  );
  }